button {
  cursor: pointer;
  font-family: $ff-robotocondensed;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  min-width: 160px;
  min-height: 40px;
  align-self: center;
  display: inline-block;
  vertical-align: middle;
  padding: 10px 15px;
  border: 1px solid $color-font-grey;
  border-radius: 3px;
  background: transparent;
  color: $color-font-grey;

  @include hover-focus {
    background: $color-default-white;
    color: $color-default-black;
  }
}
