.intro {
  padding-top: 185px;
  padding-bottom: 285px;
  position: relative;
  background-image: image-set(url("../img/bg-big.webp") 1x, url("../img/bg-big.png") 1x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: $color-font-grey;

  @include vp-1023 {
    padding-top: 80px;
    padding-bottom: 205px;
  }

  @include vp-767 {
    padding-top: 80px;
    padding-bottom: 190px;
  }


  & h1 {
    margin-top: 0;
    font-family: $ff-robotocondensed;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    margin-bottom: 32px;

    @include vp-1023 {
      font-size: 64px;
      line-height: 75px;
    }

    @include vp-767 {
      font-size: 36px;
      line-height: 42px;
      margin-bottom: 20px;
    }
  }

  & p {
    font-family: $ff-ptmono;
    font-size: 22px;
    line-height: 34px;
    margin-bottom: 40px;

    @include vp-767 {
      font-size: 14px;
      line-height: 22px;
      margin-bottom: 50px;

      & br {
        display: none;
      }
    }
  }
}

.intro__wrapper {
  margin-left: 125px;

  @include vp-1023 {
    margin-left: 0;
  }
}

.intro__button {
  @include vp-1023 {
    margin-left: 45px;
  }

  @include vp-767 {
    margin-left: 0;
  }
}

.intro button {
  font-size: 18px;
  line-height: 21px;
  background: $color-neon-blue;
  border: none;
  padding: 15px 25px;
  min-width: 410px;
  min-height: 55px;
  display: block;

  @include vp-767 {
    min-width: 270px;
    min-height: 40px;
    font-size: 14px;
    line-height: 16px;

    & span {
      display: none;
    }
  }
}
