.index-header {
  position: sticky;
  top: 0;
  z-index: 3;
  background: linear-gradient(180deg, #041827 90%, rgba(4, 24, 39, 0) 150%);
  width: 100%;
  min-height: 120px;
  flex-wrap: wrap;
  padding-top: 10px;

  @include vp-1023 {
    min-height: 100px;
  }

  @include vp-767 {
    & {
      min-height: 70px;
    }
  }
}

.index-header svg {
  fill: $color-neon-blue;
}

.index-header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-font-grey;
}

.index-header__logo-wrapper {
  @include vp-1023 {
    margin-right: auto;
  }

  @include vp-767 {
    & p {
      display: none;
    }
  }
}

.index-header__logo-wrapper svg:first-of-type {
  display: block;

  @include vp-1023 {
    display: block;
  }

  @include vp-767 {
    display: none;
  }
}

.index-header__logo-wrapper svg:last-of-type {
  display: none;

  @include vp-1023 {
    display: none;
  }

  @include vp-767 {
    display: block;
  }
}

.index-header__constacts-wrapper {
  display: inline-flex;
  gap: 97px;
  margin-right: auto;
  margin-left: auto;

  @include vp-1023 {
    gap: 20px;
    margin-right: 20px;
  }

  @include vp-767 {
    margin-right: 10px;
  }

  & a {
    color: $color-font-grey;
    text-decoration: none;
    padding: 10px;

    @include hover-focus {
      & svg {
        fill: $color-font-grey;
      }
    }
  }

  & svg {
    vertical-align: middle;
  }
}

.index-header__constacts-wrapper span {
  word-break: keep-all;

  @include vp-1023 {
    display: none;
  }
}

.index-header button {
  margin-right: 60px;
  cursor: pointer;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  min-width: 160px;
  min-height: 40px;
  align-self: center;
  display: inline-block;
  vertical-align: middle;
  padding: 10px 15px;
  border: 1px solid $color-font-grey;
  border-radius: 3px;
  background: transparent;
  color: $color-font-grey;
  text-transform: uppercase;

  @include vp-1023 {
    display: none;
  }

  @include hover-focus {
    background: $color-default-white;
    color: $color-default-black;
  }
}
