.products-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 85px;
  list-style: none;
  padding: 0;
  margin: 0;

  @include vp-1439 {
    display: flex;
    flex-wrap: wrap;
  }

  @include vp-1023 {
    row-gap: 30px;
    justify-content: center;
  }
}

.products-list li {
  width: 100%;
  max-width: 350px;
  max-height: 650px;

  position: relative;

  @include vp-1023 {
    max-width: 670px;
    min-height: 250px;
  }

  @include vp-767 {
    max-width: 670px;
  }
}

.products-list a {
  position: relative;
  width: 100%;
  height: 100%;
  display: block;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $color-default-white;
  text-decoration: none;

  &::after {
    position: absolute;
    z-index: 0;
    content: "";
    width: 60px;
    height: 60px;
    right: 0;
    bottom: 0;
    background-color: $color-neon-blue;
    background-image: url("../img/svg/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;

    @include vp-767 {
      width: 40px;
      height: 40px;
    }
  }

  @include hover-focus {
    box-shadow: inset -10px -5px 10px 5px $color-default-black;

    &::after {
      box-shadow: inset 5px 5px 10px 5px $color-default-black;
    }
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 30px;
  }
}

.products-list h3 {
  position: absolute;
  top: 50px;
  left: 40px;
  z-index: 4;

  @include vp-767 {
    top: 30px;
    left: 30px;
  }
}

.products-list img {
  object-fit: contain;
  width: 100%;
}
